import React, { Fragment ,useState, useContext} from "react";
import {Row,Col, Toast} from "reactstrap";
import copy from "copy-to-clipboard";
import {toast} from "react-toastify";
import { PrevState, Prevs, PrevData } from "./Context";

export const Great=()=>{
    const {prevs,setPrevs}=useContext(Prevs);
    const {prevData,setPrevData}=useContext(PrevData);
    const {prevstate,setPrevState}=useContext(PrevState);
    const copied=(event)=>{
            if(prevstate){
            setPrevData(event.target.textContent);
            console.log(prevData);
            prevs.textContent=prevData;
            event.target.textContent="Copied";
            setPrevState(true);
            setPrevs(event.target);
            }
            else if(!prevstate)
            {
            setPrevData(event.target.textContent);
            event.target.textContent="Copied";
            setPrevState(true);
              setPrevs(event.target);
            }
            
    }
    return(
        <>
        <h4 style={{marginLeft:"15px",marginTop:"10px",marginBottom:"15px"}}>Might Be Great</h4>
        <div className="flex-container" style={{display:"flex",width:"100%"}}>
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#1abc9c",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#1abc9c");
            copied(e);
            }} >
            #1abc9c
            </div>
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#3498db",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#3498db");
            copied(e);
            }} >
            #3498db
            </div>
            
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#8e44ad",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#8e44ad");
            copied(e);
            }} >
            #8e44ad
            </div>
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#e74c3c",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#e74c3c");
            copied(e);
            }} >
           #e74c3c
            </div>
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#d35400",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#d35400");
            copied(e);
            }} >
            #d35400
            </div>
     </div>
     <div className="flex-container" style={{display:"flex",width:"100%",marginTop:"5px"}}>
     <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#cc8339",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#cc8339");
            copied(e);
            }} >
           #f39c12
            </div>
             
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#2c3e50",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#2c3e50");
            copied(e);
            }} >
           #2c3e50
            </div>
        <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#f1c40f",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#f1c40f");
            copied(e);
            }} >
            #f1c40f
            </div>
           
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#bdc3c7",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#bdc3c7");
            copied(e);
            }} >
           #bdc3c7
            </div>
            
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#27ae60",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#27ae60");
            copied(e);
            }} >
            #27ae60
            </div>
     </div>
    </>
    )
}