import React, { Fragment ,useState, useContext} from "react";
import {Row,Col, Toast} from "reactstrap";
import copy from "copy-to-clipboard";
import {toast} from "react-toastify";
import { PrevState, Prevs, PrevData } from "./Context";

export const Skin=()=>{
    const {prevs,setPrevs}=useContext(Prevs);
    const {prevData,setPrevData}=useContext(PrevData);
    const {prevstate,setPrevState}=useContext(PrevState);
    const copied=(event)=>{
            if(prevstate){
            setPrevData(event.target.textContent);
            console.log(prevData);
            prevs.textContent=prevData;
            event.target.textContent="Copied";
            setPrevState(true);
            setPrevs(event.target);
            }
            else if(!prevstate)
            {
            setPrevData(event.target.textContent);
            event.target.textContent="Copied";
            setPrevState(true);
              setPrevs(event.target);
            }
            
    }
    return(
        <>
        <h4 style={{marginLeft:"15px",marginTop:"10px",marginBottom:"15px"}}>Skin</h4>
        <div className="flex-container" style={{display:"flex",width:"100%"}}>
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#FFDBAC",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#FFDBAC");
            copied(e);
            }} >
            #FFDBAC
            </div>
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#F1C27D",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#F1C27D");
            copied(e);
            }} >
            #F1C27D
            </div>
            
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#E0AC69",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#E0AC69");
            copied(e);
            }} >
            #E0AC69
            </div>
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#C68642",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#C68642");
            copied(e);
            }} >
           #C68642
            </div>
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#8D5524",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#8D5524");
            copied(e);
            }} >
            #8D5524
            </div>
     </div>
     <div className="flex-container" style={{display:"flex",width:"100%",marginTop:"5px"}}>
     <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#F5C469",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#F5C469");
            copied(e);
            }} >
           #F5C469
            </div>
             
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#E5B143",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#E5B143");
            copied(e);
            }} >
           #E5B143
            </div>
        <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#F3B63A",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#F3B63A");
            copied(e);
            }} >
            #F3B63A
            </div>
           
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#F3B431",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#F3B431");
            copied(e);
            }} >
           #F3B431
            </div>
            
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#DFAF2B",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#DFAF2B");
            copied(e);
            }} >
            #DFAF2B
            </div>
     </div>
    </>
    )
}