import React, { Fragment ,useState, useContext} from "react";
import {Row,Col, Toast} from "reactstrap";
import copy from "copy-to-clipboard";
import {toast} from "react-toastify";
import { PrevState, Prevs, PrevData } from "./Context";

export const Grey=()=>{
    const {prevs,setPrevs}=useContext(Prevs);
    const {prevData,setPrevData}=useContext(PrevData);
    const {prevstate,setPrevState}=useContext(PrevState);
    const copied=(event)=>{
            if(prevstate){
            setPrevData(event.target.textContent);
            console.log(prevData);
            prevs.textContent=prevData;
            event.target.textContent="Copied";
            setPrevState(true);
            setPrevs(event.target);
            }
            else if(!prevstate)
            {
            setPrevData(event.target.textContent);
            event.target.textContent="Copied";
            setPrevState(true);
              setPrevs(event.target);
            }
            
    }
    return(
        <>
        <h4 style={{marginLeft:"15px",marginTop:"10px",marginBottom:"15px"}}>Grey</h4>
        <div className="flex-container" style={{display:"flex",width:"100%"}}>
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#D2D7D3",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#D2D7D3");
            copied(e);
            }} >
            #D2D7D3
            </div>
            
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#BDC3C7",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#BDC3C7");
            copied(e);
            }} >
            #BDC3C7
            </div>
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#BFBFBF",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#BFBFBF");
            copied(e);
            }} >
            #BFBFBF
            </div>
            
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#ABB7B7",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#ABB7B7");
            copied(e);
            }} >
           #ABB7B7
            </div>
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#95A5A6",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#95A5A6");
            copied(e);
            }} >
            #95A5A6
            </div>
     </div>
     <div className="flex-container" style={{display:"flex",width:"100%",marginTop:"5px"}}>
     <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#616C6F",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#616C6F");
            copied(e);
            }} >
            #616C6F
            </div>
     <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#586776",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#586776");
            copied(e);
            }} >
            #586776
            </div>
        
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#535C68",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#535C68");
            copied(e);
            }} >
            #535C68
            </div>
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#47535E",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#47535E");
            copied(e);
            }} >
           #47535E
            </div>
            
            <div class="box flex-child"  style={{borderRadius:"6px",backgroundColor:"#2F363F",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#2F363F");
            copied(e);
            }} >
          #2F363F
            </div>
     </div>
    </>
    )
}