import React, { Fragment ,useState, useContext} from "react";
import {Row,Col, Toast} from "reactstrap";
import copy from "copy-to-clipboard";
import {toast} from "react-toastify";
import { PrevState, Prevs, PrevData } from "./Context";

export const Dark=()=>{
    const {prevs,setPrevs}=useContext(Prevs);
    const {prevData,setPrevData}=useContext(PrevData);
    const {prevstate,setPrevState}=useContext(PrevState);
    const copied=(event)=>{
            if(prevstate){
            setPrevData(event.target.textContent);
            console.log(prevData);
            prevs.textContent=prevData;
            event.target.textContent="Copied";
            setPrevState(true);
            setPrevs(event.target);
            }
            else if(!prevstate)
            {
            setPrevData(event.target.textContent);
            event.target.textContent="Copied";
            setPrevState(true);
              setPrevs(event.target);
            }
            
    }
    return(
        <>
        <h4 style={{marginLeft:"15px",marginTop:"10px",marginBottom:"15px"}}>Dark Colors</h4>
        
        <div className="flex-container" style={{display:"flex",width:"100%"}}>
        <div class="box flex-child"  style={{color:"#ffffff",borderRadius:"6px",backgroundColor:"#004d38",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#004d38");
            copied(e);
            }} >
           #004d38
            </div>
            <div class="box flex-child"  style={{color:"#ffffff",borderRadius:"6px",backgroundColor:"#01113d",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#01113d");
            copied(e);
            }} >
            #01113d
            </div>
            <div class="box flex-child"  style={{color:"#ffffff",borderRadius:"6px",backgroundColor:"#261010",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#261010");
            copied(e);
            }} >
            #261010
            </div>
            
            <div class="box flex-child"  style={{color:"#ffffff",borderRadius:"6px",backgroundColor:"#2b2238",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#2b2238");
            copied(e);
            }} >
           #2b2238
            </div>
            
            <div class="box flex-child"  style={{color:"#ffffff",borderRadius:"6px",backgroundColor:"#204252",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#204252");
            copied(e);
            }} >
            #204252
            </div>
     </div>
     <div className="flex-container" style={{display:"flex",width:"100%",marginTop:"5px"}}>
     <div class="box flex-child"  style={{color:"#ffffff",borderRadius:"6px",backgroundColor:"#3d2f02",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#3d2f02");
            copied(e);
            }} >
           #3d2f02
            </div>
            <div class="box flex-child"  style={{color:"#ffffff",borderRadius:"6px",backgroundColor:"#004004",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#004004");
            copied(e);
            }} >
            #004004
            </div>
            
            <div class="box flex-child"  style={{color:"#ffffff",borderRadius:"6px",backgroundColor:"#750017",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#750017");
            copied(e);
            }} >
            #750017
            </div>
            
            <div class="box flex-child"  style={{color:"#ffffff",borderRadius:"6px",backgroundColor:"#36042e",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#36042e");
            copied(e);
            }} >
           #36042e
            </div>
            
            <div class="box flex-child"  style={{color:"#ffffff",borderRadius:"6px",backgroundColor:"#003c45",paddingTop:"70px",textAlign:"center"}} 
            onClick={(e)=>{copy("#003c45");
            copied(e);
            }} >
            #003c45
            </div>
     </div>

    </>
    )
}